.sortLabel {
	display: inline-block;
	margin-left: 10px;
	cursor: pointer;

	@media (max-width: 600px) {
		margin-left: 5px;
	}

	@media (max-width: 370px) {
		margin-left: 0;
	}

	> svg {
		color: #9e9e9e;
		font-size: 12px;

		@media (max-width: 600px) {
			font-size: 8px;
		}
	}
}
